<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-account-setting</v-icon>
      Pengaturan Akun
    </v-card-title>
    <v-divider></v-divider>
    <v-form v-model="valid" ref="form">
      <v-card-text>
        <v-text-field
          v-model="dataPassword.old_password"
          label="Password lama"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
          :append-icon="showpasswordLama ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showpasswordLama ? 'text' : 'password'"
          @click:append="showpasswordLama = !showpasswordLama"
        ></v-text-field>
        <v-text-field
          v-model="dataPassword.new_password"
          label="Password baru"
          outlined
          dense
          :rules="passwordRule"
          autocomplete="off"
          :append-icon="showpasswordBaru ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showpasswordBaru ? 'text' : 'password'"
          @click:append="showpasswordBaru = !showpasswordBaru"
        ></v-text-field>
        <v-text-field
          :disabled="dataPassword.new_password===''?true:false"
          v-model="dataPassword.confirm_password"
          label="Konfirmasi password"
          outlined
          dense
          :rules="konfirmasiPassword"
          autocomplete="off"
          :type="showpasswordBaru ? 'text' : 'password'"
        ></v-text-field>

        <Notif :msg="msg" :status="status" @visible="visible" />
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          @click="$router.go(-1)"
          text>
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          :loading="isLoading"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
  import { putDataLogin } from '@/config/login'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Password berhasil berubah',
          error: 'Password gagal diubah',
          visible: false
        },
        dataPassword: {
          old_password: '',
          new_password: '',
          confirm_password: '',
        },
        showpasswordLama: false,
        showpasswordBaru: false,
        status: null,
        isLoading: false
      }
    },

    components: {Notif},

    computed: {
      passwordRule() {
        return [
          ...this.requiredRule,
          v => (v.length >= 6) || 'Harus lebih dari 6 karakter'
        ]
      },
      konfirmasiPassword() {
        return [ 
          v => !!v || 'Data harus diisi',
          v => (v === this.dataPassword.new_password) || 'Password tidak cocok' 
        ]
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.isLoading = true
          putDataLogin(this.dataPassword)
            .then((res) => {
              if (res.data.status) {
                this.status = true
                this.isLoading = false
              } else {
                this.status = false
                this.isLoading = false
              }
            })
            .catch(() => {
              this.status = false
              this.isLoading = false
            })
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
  }
</script>
